.idp-btn {
    direction: ltr;
    font-weight: 700;
    height: auto;
    line-height: normal;
    max-width: 220px;
    min-height: 40px;
    padding: 8px 16px;
    text-align: left;
    width: 100%;
    border: none;
    border-radius: 2px;
    position: relative;
    margin: 0;
    min-width: 64px;
    display: inline-block;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0;
    overflow: hidden;
    outline: 0;
    cursor: pointer;
    text-decoration: none;
    vertical-align: middle;
}
.idp-icon {
    border: none; 
    display: inline-block;    
    height: 18px;    
    vertical-align: middle;    
    width: 18px;
}
.idp-text {
    font-size: 14px;
    padding-left: 16px;
    text-transform: none;
    vertical-align: middle;
}