.live-ico {
    display: inline-block;
    width: 0.5rem;
    height: 0.5rem;
    vertical-align: -0.125rem;
    border-radius: 50%;
    animation: 0.75s linear infinite alternate-reverse live-animation;
    background-color: #ffffff;
}
@keyframes live-animation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}