div.signInState {
    gap: 0.4rem;
    font-size: 12px;
    background-color: var(--main-bg);
}
div.signInState::before, div.signInState::after {
    text-align: center;
    z-index: 0;
    width: 2.5rem;
    padding: 0px 0.5rem;
}
div.signInState::before {
    content: "Out";
}
div.signInState::after {
    content: "In";
}
div.signInState > span {
    position: absolute;
    width: 2.5rem;
    height: 1rem;
    top: 50%;
    margin-left: 0px;
    translate: 0% -50%;
    border-radius: 50rem;
    z-index: -1;
    transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
div.signInState[data-in="false"] > span {
    background-color: var(--bs-danger);
}
div.signInState[data-in="true"] > span {
    background-color: var(--bs-success);
    margin-left: 2.9rem;
}

@keyframes userInAnimation {
    0% {
        transform: translate(0%, 100%);
    }
    100% {
        transform: none;
    }
}

li.userIn {
    animation: userInAnimation 0.5s cubic-bezier(0, 0, 0.2, 1);
}


@media (prefers-reduced-motion) {
    li.userIn {
        animation: none !important;
    }
    div.signInState > span {
        transition: none !important;
    }
}