:root {
    --theme1: #2892D7;
    --theme2: #6DAEDB;
    --theme3: #1D70A2;
    --theme3-rgb: 29, 112, 162;
    --accent: #EF476F;
    --accent-rgb: 239, 71, 111;
    --main-bg: #071a22;
    --main-bg-rgb: 7, 26, 34;
    --bg-accent1: #5d51ba74;
    --bg-accent2: #5169ba29;

    --slideFill-bg: linear-gradient(90deg, rgba(var(--theme3-rgb), 0.5), var(--theme1));
}

[data-bs-theme=light] {
    --main-bg: #cceff5;
    --main-bg-rgb: 204, 239, 245;
}

.btn {
    --bs-btn-active-bg: var(--bs-body-bg);
    --bs-btn-focus-shadow-rgb: 13, 110, 253;
}

.lead-bg {
    background: radial-gradient(circle at 0% 0%, var(--bg-accent1), transparent 25%), radial-gradient(circle at 100% 0%, var(--bg-accent2), transparent 50%), var(--main-bg);
    position: relative;
    z-index: 1;
}


svg.svgArt {
    position: absolute;
    top: 50%;
    right: 0px;
    aspect-ratio: 1 / 1;
    height: 50%;
    opacity: 0.5;
    z-index: -1;
    transition: all 0.4s cubic-bezier(0, 0, 0.2, 1);
    will-change: top, right;
}

svg.svgArt > polygon.tri {
    fill: var(--theme1);
    transform: rotate(90deg);
    transform-origin: 100% 100%;
    transition: transform 0.4s cubic-bezier(0, 0, 0.2, 1);
    will-change: transform;
}

svg.svgArt > circle.circle {
    fill: var(--theme2);
    transform: translate(100%, 0%);
    transition: transform 0.6s cubic-bezier(0.3, 1.05, 0.41, 1.07);
    will-change: transform;
}

svg.svgArt > rect.pill {
    fill: var(--theme3);
    transform: translate(0%, 50%);
    transform-origin: 75% 100%;
    transition: transform 0.4s cubic-bezier(0, 0, 0.2, 1);
    will-change: transform;
}


.btn-go {
    --bs-btn-border-color: var(--bs-secondary-bg);
    --bs-btn-hover-border-color: rgba(var(--theme3-rgb), 0.5);
    background: var(--bs-button-bg, transparent);
    padding: 0.5rem 1rem;
}

.btn-a {
    text-align: start;
    display: inline-flex;
    justify-content: space-between;
    gap: 0.5rem;
}

.btn-go.btn-fill {
    --bs-button-bg: rgba(var(--accent-rgb), 0.75);
    --bs-btn-border-color: transparent;
}

.btn-go.btn-fill2 {
    --bs-button-bg: rgba(var(--theme3-rgb), 0.75);
    --bs-btn-border-color: transparent;
}

.btn-slideFill {
    background: var(--slideFill-bg);
    background-size: 0px 100%;
    background-repeat: no-repeat;
    transition: background-size 0.5s ease;
}

.btn-slideFill > i.bi {
    transform: translateX(0%);
    transition: transform 0.5s ease;
}

.btn-slideFill:hover {
    background-size: 100% 100%;
}

.btn-slideFill:hover > i.bi {
    transform: translateX(25%);
}

div.card.clear-card {
    --bs-card-bg: transparent;
}

div.card.blur-card {
    --bs-card-bg: transparent;
    backdrop-filter: blur(20px);
}

input.blur-input, input.blur-input~label::after {
    --bs-body-bg: transparent;
    backdrop-filter: blur(0px);
    will-change: backdrop-filter, background-color;
    transition: backdrop-filter ease 0.2s, background-color ease 0.2s;
}

input.blur-input:focus {
    --bs-body-bg: rgba(var(--main-bg-rgb), 0.7);
    backdrop-filter: blur(10px);
}


div.accordion-clear {
    --bg: rgba(var(--main-bg-rgb), 0.2);

    --bs-accordion-active-color: var(--bs-body-color);
    --bs-accordion-bg: var(--bg);
    --bs-accordion-active-bg: var(--bg);
}

div.accordion-clear .accordion-button, div.accordion-clear .accordion-collapse {
    backdrop-filter: blur(10px);
}

.list-group-clear {
    --bs-list-group-bg: transparent;
}

.list-group-blur {
    backdrop-filter: blur(10px);
}

@keyframes blurIn {
    0% {
        background-color: rgba(var(--main-bg-rgb), 0);
        backdrop-filter: blur(0px);
    }
}

div.errorOverlay {
    background-color: rgba(var(--main-bg-rgb), 0.75);
    backdrop-filter: blur(10px);
    animation: blurIn 0.5s ease;
}


@media (prefers-reduced-motion) {
    svg.svgArt > * {
        transition: none !important;
    }
    input.blur-input, input.blur-input~label::after {
        transition: none !important;
    }
    div.errorOverlay {
        animation: none !important;
    }
    .btn-slideFill {
        transition: none !important;
    }
    .btn-slideFill > i.bi {
        transition: none !important;
    }
}